import React from "react";

export default function MealDetails(props) {
    if(!props.isShowing){
        return null;
    }
    return (
        <div className='bg-dark' style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }} >
            {props.showMealDetails(props.selectedMeal)}
        </div>
    )
}